	import {  
	    mapMutations  
	} from 'vuex';
	export default{
		data(){
			return {
				headerPosition:"fixed",
				headerTop:"0px",
				accountBtnSee:false,
				passBtnSee:false,
				user:{
					account:"",
					password:""
				},
				isActive:false
			}
		},
		onLoad(options){
			
		},
		methods:{
			...mapMutations(['login']),
			inputChange(e){
				var val = e.detail.value;
				if(typeof(val) != "undefined" && val != ""){
					var id = e.currentTarget.id;
					if("account" == id){
						this.accountBtnSee = true;
					}else{
						this.passBtnSee = true;
					}
					//登录按钮开关
					if(this.user.account != "" && this.user.password != ""){
						this.isActive = true;
					}else{
						this.isActive = false;
					}
				}else{
					this.accountBtnSee = false;
					this.passBtnSee = false;
					this.isActive = false;
				}
			},
			clearInput(e){
				var id = e.currentTarget.id;
				if(typeof(id) != "undefined" && id != ""){
					if("accountClear" == id){
						this.user.account = "";
						this.accountBtnSee = false;
					}else{
						this.user.password = "";
						this.passBtnSee = false;
					}
					this.isActive = false;
				}
			},
			async toLogin(){
				if(this.isActive){
					this.logining = true;
				}else{
					return;
				}
				/* 数据验证模块
				if(!this.$api.match({
					mobile,
					password
				})){
					this.logining = false;
					return;
				}
				*/
			   //发送get请求
			   uni.request({
			   	   url:"http://192.168.1.4:8080/web/api/userinfo/login",
				   method:"GET",
				   data:{userName:this.user.account,password:this.user.password},
				   success: res => {
				   	 console.log(res);
				   },
				   fail: () => {
				   	
				   }
			   });
/*                this.$http.get('',{params : {}}).then(function(res){
                    console.log(res); 
                },function(){
                    console.log('请求失败处理');
                }); */
				const result = await this.$api.json('userInfo');
				if(result.status === 1){
					this.login(result.data);
			        uni.navigateBack();  
				}else{
					this.$api.msg(result.msg);
					this.logining = false;
				}
			}
		}
	}
